/* home.css */

body {
  background: linear-gradient(120deg, #e0ffe8 0%, #f7f7f7 100%);
  min-height: 100vh;
}

.home-container {
  max-width: 1100px;
  margin: 4rem auto;
  padding: 0 1rem;
  text-align: center;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #333;
}

.hero {
  background: #ffffffcc;
  border-radius: 18px;
  box-shadow: 0 8px 32px rgba(46, 204, 113, 0.10);
  padding: 3rem 2rem 2.5rem 2rem;
  margin-bottom: 3rem;
  position: relative;
}

.hero h1 {
  color: #27ae60;
  font-size: 3.5rem;
  margin-bottom: 0.5rem;
  font-weight: 800;
  letter-spacing: -1px;
}

.subtitle {
  font-size: 1.35rem;
  color: #555;
  margin-bottom: 2.5rem;
  max-width: 650px;
  margin-left: auto;
  margin-right: auto;
}

.cta-button {
  background: linear-gradient(90deg, #2ecc71 60%, #27ae60 100%);
  color: white;
  padding: 1.1rem 3.2rem;
  font-size: 1.25rem;
  font-weight: 700;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  box-shadow: 0 6px 18px rgba(46, 204, 113, 0.18);
  transition: background 0.3s, transform 0.2s;
}

.cta-button:hover {
  background: linear-gradient(90deg, #27ae60 60%, #2ecc71 100%);
  transform: translateY(-2px) scale(1.04);
}

.features {
  margin-top: 4rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2.5rem;
}

.feature-card {
  background: #ecf9f1;
  border-radius: 16px;
  box-shadow: 0 8px 24px rgba(46, 204, 113, 0.10), 0 1.5px 6px rgba(0,0,0,0.04);
  padding: 2.2rem 1.7rem;
  width: 290px;
  min-width: 220px;
  transition: transform 0.3s, box-shadow 0.3s;
  cursor: default;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.feature-card:hover {
  transform: translateY(-8px) scale(1.03);
  box-shadow: 0 16px 32px rgba(46, 204, 113, 0.13), 0 2px 8px rgba(0,0,0,0.06);
}

.feature-icon {
  font-size: 3.8rem;
  margin-bottom: 1rem;
  filter: drop-shadow(0 2px 6px #b2f2d6);
}

.feature-title {
  color: #2ecc71;
  font-weight: 700;
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.feature-description {
  font-size: 1.08rem;
  color: #333;
  line-height: 1.5;
}

.cta-section {
  margin-top: 5rem;
  background: #f7f7f7;
  border-radius: 16px;
  padding: 2.5rem 1.5rem 2rem 1.5rem;
  box-shadow: 0 4px 16px rgba(46, 204, 113, 0.07);
}

.cta-section h2 {
  font-size: 2.1rem;
  color: #27ae60;
  font-weight: 800;
  margin-bottom: 1rem;
}

.cta-section p {
  font-size: 1.13rem;
  color: #555;
  max-width: 520px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 2rem;
}

footer {
  margin-top: 6rem;
  color: #999;
  font-size: 0.95rem;
  text-align: center;
  padding-bottom: 2rem;
}

@media (max-width: 900px) {
  .features {
    gap: 1.2rem;
  }
  .feature-card {
    width: 90vw;
    max-width: 350px;
  }
}

@media (max-width: 600px) {
  .home-container {
    padding: 0 0.2rem;
  }
  .hero h1 {
    font-size: 2.1rem;
  }
  .features {
    flex-direction: column;
    gap: 1.2rem;
  }
  .feature-card {
    width: 98vw;
    max-width: 99vw;
    padding: 1.2rem 0.5rem;
  }
  .cta-section {
    padding: 1.2rem 0.5rem 1.2rem 0.5rem;
  }
}
